<template>
    <main class="main-content">
        <h1>{{ 'successMessage' | translate }} <span>{{ amountFrom }}{{ currencyFrom }} &#8594; {{ amountTo }}{{ currencyTo }}</span></h1>
    </main>
</template>

<script>
export default {
    name: 'SuccessMessage',
    computed: {
        currencyFrom: function() {
            return this.$store.getters.currencyFrom;
        }, 
        currencyTo: function() {
            return this.$store.getters.currencyTo;
        }, 
        amountFrom: function() {
            return this.$store.getters.amountFrom;
        }, 
        amountTo: function() {
            return this.$store.getters.amountTo;
        } 
    }
}
</script>

<style scoped>
span {
    color: rgb(65, 67, 156);
}
</style>