import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        currencyFrom: '',
        amountFrom: '0',
        currencyTo: '',
        amountTo: '0',
        reserveBTC: 100,
        reserveUAH: 300000,
        isRoute: false
    },
    getters: {
        currencyFrom: state => {
            return state.currencyFrom;
        },
        amountFrom: state => {
            return state.amountFrom;
        },
        currencyTo: state => {
            return state.currencyTo;
        },
        amountTo: state => {
            return state.amountTo;
        },
        reserveBTC: state => {
            return state.reserveBTC;
        },
        reserveUAH: state => {
            return state.reserveUAH;
        }
    },
    mutations: {
        setcurrencyFrom: (state, newValue) => {
            return state.currencyFrom = newValue;
        },
        setAmountFrom: (state, newValue) => {
            state.amountFrom = newValue;
        },
        setcurrencyTo: (state, newValue) => {
            state.currencyTo = newValue;
        },
        setAmountTo: (state, newValue) => {
            state.amountTo = newValue;
        },
        setReserveBTC: (state, newValue) => {
            state.reserveBTC = newValue;
        },
        setReserveUAH: (state, newValue) => {
            state.reserveBTC = newValue;
        }
    }
})